.quant-card {
  background-color: #fff;
  border-radius: 12px;
  padding: 12px;
  margin-bottom: 12px;
  box-shadow: 0 2px 6px rgba(0,0,0,0.05);
}

/* Header */
.quant-header {
  display: flex;
  align-items: center;
  margin-bottom: 12px;
}
.icon-circle {
  width: 36px;
  height: 36px;
  border-radius: 50%;
  background-color: #00a37a;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 8px;
}
.icon-text {
  color: #fff;
  font-weight: bold;
}
.header-text .title {
  font-size: 14px;
  font-weight: bold;
}
.header-text .sub-text {
  color: #777;
  font-size: 10px;
}

/* 投资&利润大边框 */
.profit-section-border {
  display: flex;
  border-radius: 12px;
  border: 1px solid #00a37a; /* 默认绿色 */
  overflow: hidden;
  margin-bottom: 8px;
  transition: border-color 0.3s ease;
}

/* 边框颜色动态 */
.profit-positive {
  border-color: #00a37a;
}
.profit-negative {
  border-color: #ff4d4f;
}

/* 左侧：实际投资 */
.profit-left {
  flex: 1;
  padding: 8px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #f9f9f9;
}
.profit-left .label {
  font-size: 10px;
  color: #666;
  margin-bottom: 4px;
}
.profit-left .value {
  font-size: 14px;
  font-weight: bold;
  color: #000;
}

/* 右侧：总利润 */
.profit-right {
  flex: 1.5;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  transition: background-color 0.3s ease;
}

.profit-bg-green {
  background-color: #00a37a;
}

.profit-bg-red {
  background-color: #ff4d4f;
}

.profit-right-inner {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  padding: 12px;
}

.profit-right-inner .label {
  font-size: 10px;
  color: #fff;
  margin-bottom: 4px;
}

.value-right {
  font-size: 12px;
  font-weight: bold;
  color: #fff;
}
