/* src/styles/LPDetail.css */

/* 按钮区域的容器 */
.lp-button-container {
  display: flex;
  justify-content: center;
  gap: 15px;
  flex-wrap: wrap;
  margin-top: 20px;
}

/* 按钮样式 */
.lp-button-container .action-btn {
  padding: 8px 12px;
  font-size: 12px;
  width: 50%;
  font-weight: 800;
  border-radius: 12px;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

/* 按钮颜色变化 */
.lp-button-container .action-btn:hover {
  opacity: 0.9;
}

/* 领取奖励按钮 */
.lp-button-container .action-btn:nth-child(1) {
  background-color: #10B981;
  color: #fff;
}

.lp-button-container .action-btn:nth-child(1):hover {
  background-color: #059669;
}

/* 存入LP按钮 */
.lp-button-container .action-btn:nth-child(2) {
  background-color: #0EA5E9;
  color: #fff;
}

.lp-button-container .action-btn:nth-child(2):hover {
  background-color: #0284C7;
}


.lp-button-container .action-btn:nth-child(3):hover {
  background-color: #D63D3D;
}
