.add-liquidity.old-layout {
  background: #fff;
  border-radius: 16px;
  padding: 12px;
}

/* 顶部信息 */
.top-info-grid {
  display: grid;
  grid-template-columns: 1fr 12px 1fr;
  gap: 8px;
  align-items: stretch;
}

.divider-vertical {
  width: 1px;
  background: #eef2f6;
}

.divider-horizontal {
  height: 1px;
  background: #eef2f6;
  margin: 12px 0;
}

/* 钱包余额 */
.wallet-box, .pool-box {
  border: 1px solid #e5e7eb;
  border-radius: 12px;
  padding: 10px 12px;
  background: linear-gradient(145deg, #f8fafc, #eef2f6);
  box-shadow: 0 2px 8px rgba(0,0,0,0.04);
}

.wallet-title, .pool-title, .mine-title {
  font-weight: 800;
  font-size: 13px;
  margin-bottom: 6px;
  color: #0f172a;
}

.wallet-row, .pool-row, .mine-row, .lock-row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 13px;
  color: #1f2937;
  padding: 3px 0;
}

/* 我的 + 锁仓 */
.mine-and-lock {
  display: grid;
  grid-template-columns: 1fr 12px 1fr;
  gap: 8px;
}

.mine-box, .lock-box {
  border: 1px solid #e5e7eb;
  border-radius: 12px;
  padding: 10px 12px;
  background: #fff;
}

.mine-lp {
  margin-top: 4px;
  font-size: 12px;
  color: #64748b;
}

/* Tabs */
.tabs {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 8px;
}

.tab {
  border: 1px solid #e5e7eb;
  background: #f8fafc;
  color: #0f172a;
  font-weight: 700;
  padding: 8px 0;
  border-radius: 10px;
  cursor: pointer;
}
.tab.active {
  background: linear-gradient(180deg, #dff7e8, #c6f0d5);
  border-color: #bde6c9;
  color: #065f46;
}

/* 输入区（旧风格） */
.input-group.old-stack {
  display: grid;
  gap: 8px;
  margin-top: 8px;
}
.input-item {
  display: grid;
  grid-template-columns: auto 1fr auto;
  align-items: center;
  gap: 8px;
  background: #fafafa;
  border: 1px solid #e5e7eb;
  border-radius: 10px;
  padding: 8px;
}
.input-item label {
  color: #166534;
  font-weight: 700;
  font-size: 13px;
}
.input-item .btn-max {
  background: #e6f7ec;
  color: #166534;
  border: 1px solid #ccebd6;
  border-radius: 6px;
  font-size: 12px;
  padding: 3px 8px;
  cursor: pointer;
  font-weight: 600;
  white-space: nowrap;
}

/* 操作行 */
.action-row {
  display: grid;
  grid-template-columns: 1fr 120px;
  gap: 10px;
  margin-top: 6px;
  align-items: center;
}
.action-btn {
  width: 100%;
  background: #23b26d;
  color: #fff;
  border: none;
  border-radius: 10px;
  padding: 10px;
  font-weight: 700;
  cursor: pointer;
  box-shadow: 0 2px 6px rgba(35,178,109,0.2);
}
.lock-period-display {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border: 1px solid #e5e7eb;
  border-radius: 8px;
  padding: 6px 10px;
  background: linear-gradient(90deg, #f9fafb 0%, #f3fef7 100%);
  cursor: pointer;
  font-size: 12px;
  font-weight: 700;
  color: #166534;
}
.lock-period-display span {
  margin-left: 6px;
  font-size: 13px;
}

/* 提示 */
.op-hint {
  margin-top: 10px;
  font-size: 12px;
  color: #166534;
  text-align: center;
}

/* 占位 */
.tab-placeholder {
  border: 1px dashed #e5e7eb;
  border-radius: 10px;
  padding: 12px;
  font-size: 13px;
  color: #6b7280;
  text-align: center;
}

/* 高级操作 */
.advanced-row {
  display: grid;
  grid-template-columns: auto 1fr;
  align-items: center;
  gap: 8px;
}
.advanced-title {
  font-weight: 800;
  font-size: 13px;
  color: #0f172a;
}
.advanced-actions {
  display: flex;
  justify-content: flex-end;
  gap: 8px;
}
.adv-btn {
  border: 1px solid #e5e7eb;
  background: #f8fafc;
  color: #94a3b8;
  font-weight: 700;
  padding: 6px 10px;
  border-radius: 8px;
  cursor: not-allowed;
}
