/* 覆盖全屏（含 header/logo/footer）的首帧遮罩，仅在 ready=false 时展示 */
.app-splash-overlay {
  position: fixed;
  inset: 0;
  z-index: 9999;             /* 盖过 AppLayout 的头部和任何内容 */
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(255,255,255,0.9); /* 也可用纯白 #fff 或深色 #0b0b0b80 */
}

/* 简单的纯 CSS 小圆圈，你也可以换成你自己的 <Loading/> */
.app-splash-loading .spinner {
  width: 48px;
  height: 48px;
  border: 4px solid rgba(0,0,0,0.1);
  border-top-color: rgba(0,0,0,0.6);
  border-radius: 50%;
  animation: spin 0.9s linear infinite;
}

@keyframes spin {
  to { transform: rotate(360deg); }
}
