/* 遮罩层：铺满屏幕，落在页面内容之上、底栏之下 */
.bn-overlay {
  position: fixed;
  inset: 0;
  background: transparent;       /* 不用半透明，保持简洁；要暗幕可用 rgba(0,0,0,.15) */
  z-index: 998;                  /* 小于底栏+子菜单的 z-index */
}

/* 底部栏：确保在遮罩之上，子菜单再高一层 */
.bottom-nav {
  position: fixed;
  left: 0; right: 0; bottom: 0;
  height: 56px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  align-items: center;
  background: #fff;
  border-top: 1px solid #e5e7eb;
  z-index: 999;
}

/* 子菜单：固定定位在底栏上方，宽度=屏幕 1/4，并水平居中 */
.bottom-submenu {
  position: fixed;
  bottom: 56px;                 /* 贴住底栏上边缘 */
  left: 50%;
  transform: translateX(-50%);  /* 居中 */
  width: 35vw;                  /* 👈 屏幕宽度的 1/4 */
  display: grid;
  grid-auto-flow: row;
  gap: 8px;
  padding: 10px 12px;
  background: rgba(255,255,255,0.88);
  backdrop-filter: blur(8px) saturate(1.02);
  border: 1px solid #e5e7eb;
  border-radius: 12px;
  box-shadow: 0 -8px 24px rgba(0,0,0,0.08);
  z-index: 1000;                /* 高于底栏与遮罩 */
}

.bottom-submenu button {
  width: 100%;
  text-align: left;
  padding: 10px 12px;
  border-radius: 10px;
  border: 1px solid #e5e7eb;
  background: #fff;
  font-size: 14px;
}

.bottom-submenu button:active {
  transform: translateY(0);     /* 移动端点击别抖动 */
}

/* === 仅覆盖底栏“图标+文字”的样式，不改容器与子菜单 === */
.bottom-nav .nav-item {
  /* 统一 NavLink 与 button 的视觉 */
  display: flex;
  flex-direction: column;    /* 图标在上、文字在下 */
  align-items: center;
  justify-content: center;   /* 垂直居中 */
  gap: 3px;                  /* 图标与文字间距 */
  height: 100%;
  min-width: 68px;           /* 紧凑 */
  padding: 4px 8px;
  border-radius: 12px;
  background: none;
  border: 0;
  outline: none;
  text-decoration: none;
  cursor: pointer;

  /* 颜色 & 过渡（来自你的最后版本） */
  font-size: 11px;
  color: var(--nav-text-dim, #64748b);
  transition: transform .12s ease, color .12s ease, background .12s ease, box-shadow .12s ease;
}

/* 避免 SVG 基线偏移导致的“看起来不居中” */
.bottom-nav .nav-item svg {
  display: block;
  line-height: 1;
}

/* 文案行高与继承颜色 */
.bottom-nav .nav-item .nav-label {
  margin-top: 2px;
  line-height: 1;
  font-size: 11px;
  color: inherit;
}

/* hover 效果（轻微上浮+浅底） */
.bottom-nav .nav-item:hover {
  color: var(--nav-text, #334155);
  transform: translateY(-1px);
  background: rgba(2, 6, 23, 0.02);
  box-shadow: inset 0 0 0 1px rgba(2, 6, 23, 0.05);
}

/* 选中/展开态高亮（NavLink.active / aria-current / 我们给的 is-open 类） */
.bottom-nav .nav-item.active,
.bottom-nav .nav-item[aria-current="page"],
.bottom-nav .nav-item.is-open {
  color: var(--nav-active, #0ea5e9);
  box-shadow: inset 0 0 0 1px var(--glow-soft, rgba(14,165,233,0.18));
}
/* —— 缩小选中/展开高亮框到约一半 —— */

/* 先关掉原来的大框（box-shadow 那个） */
.bottom-nav .nav-item.active,
.bottom-nav .nav-item[aria-current="page"],
.bottom-nav .nav-item.is-open {
  box-shadow: none;           /* 移除之前的大描边 */
}

/* 需要定位容器 */
.bottom-nav .nav-item { position: relative; }

/* 用伪元素画一个更小的框：宽高≈容器的 56%（约一半视觉面积） */
.bottom-nav .nav-item.active::after,
.bottom-nav .nav-item[aria-current="page"]::after,
.bottom-nav .nav-item.is-open::after {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  width: 56%;
  height: 80%;
  transform: translate(-50%, -50%);
  border-radius: 10px;
  border: 1px solid var(--glow-soft, rgba(14,165,233,0.18));
  pointer-events: none;       /* 不挡点击 */
}

/* hover 时也用小框（更淡），避免看起来又大了 */
.bottom-nav .nav-item:hover::after {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  width: 56%;
  height: 56%;
  transform: translate(-50%, -50%);
  border-radius: 10px;
  border: 1px solid rgba(2, 6, 23, 0.06);
  pointer-events: none;
}

/* 同时把 hover 效果弱化，避免“大片底色”显得框很大 */
.bottom-nav .nav-item:hover {
  background: transparent;
  transform: translateY(-0.5px);
  box-shadow: none;
}
