.withdraw-card {
  background: #fff;
  border-radius: 24px;
  padding: 24px; /* 增加内边距 */
  box-shadow: 0 6px 18px rgba(0,0,0,0.12);
  margin-top: 12px;
  max-width: 400px; /* 限制宽度 */
  margin-left: auto;
  margin-right: auto;
}

.number-input {
  width: 100%;
  padding: 12px;
  border-radius: 12px;
  border: 1px solid #ccc;
  font-size: 12px;
  margin-bottom: 8px;
  box-sizing: border-box; /* 防止宽度超出 */
}
