.footer {
  background: #343c49;
  color: #aaa;
  padding: 10px 0px;
  margin-top: 60px;
  /* border-top: 1px solid #222; */
}

.footer-container {
  max-width: 1200px;
  margin: 0 auto;
  text-align: center;
}
.footer-contract{
  font-size: 14px;
}

.footer-logo {
  font-size: 1.5rem;
  font-weight: 700;
  color: #fff;
  margin-bottom: 12px;
}

.footer-links {
  display: flex;
  justify-content: center;
  gap: 20px;

}

.footer-links a {
  color: #ffffff;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  transition: all 0.25s ease;
}

.footer-links a:hover {
  color: #fff;
}

.footer-socials {
  display: flex;
  justify-content: center;
  gap: 16px;
  margin: 8px 10px;
}

.footer-socials a {
  color: #ccc;
  font-size: 20px;
  transition: transform 0.2s, color 0.2s;
}

.footer-socials a:hover {
   color: #ccc;
  transform: scale(1.15);
}

.footer-copy {
  font-size: 12px;
  color: #666;
}
.footer-contract-link {
  font-family: monospace;
 color: #666;
  font-size: 12px;
  text-decoration: none;
  transition: color 0.2s, text-shadow 0.2s;
}

.footer-contract-link:hover {
  color: #1de9b6;
  text-shadow: 0 0 6px rgba(29,233,182,0.5);
}
