.quant-page {
  position: relative;
  padding: 8px;
  min-height: 100%;
  background-color: #f9f9f9;
}

/* Loading 层 */
.quant-loading-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(255, 255, 255, 0.5);
  pointer-events: none;
  z-index: 10;
}

/* 空状态提示 */
.quant-empty {
  text-align: center;
  margin-top: 50px;
  color: #888;
  font-size: 12px;
}

/* 列表容器 */
.quant-list {
  display: flex;
  flex-direction: column;
}

/* 每条量化策略卡片 */
.quant-item {
  border: 1px solid #ddd;
  border-radius: 12px;
  padding: 12px 16px;
  margin-bottom: 12px;
  background: #fff;
  box-shadow: 0 2px 6px rgba(0,0,0,0.05);
}

/* 卡片标题 */
.quant-header {
  font-weight: 600;
  margin-bottom: 6px;
  font-size: 15px;
}

/* 卡片内容 */
.quant-body {
  margin-bottom: 4px;
  font-size: 12px;
  color: #555;
}

/* 卡片底部信息 */
.quant-footer {
  font-size: 12px;
  color: #999;
}
