/* src/styles/LPCard.css */
.lp-card {
  border: 1px solid #e6e8eb;
  border-radius: 8px;
  padding: 6px;
  background: #fff;
  box-shadow: 0 2px 6px rgba(0,0,0,0.05);
}

.lp-card-top {
  display: flex;
  
  justify-content: space-between;
  align-items: center;
}

.lp-card-left {
  display: flex;
  align-items: center;
  gap: 10px;
}

.lp-card-title {
  font-size: 14px;
  font-weight: 600;
}

.lp-card-apr {
  font-size: 12px;
  display: inline-flex;
  align-items: center;
  font-weight: 800;
  padding: 6px 10px;
  border-radius: 999px;
  color: #051ceb;
  background: #e2e8f0;
}

.lp-card-value {
  margin-top: 8px;
  font-size: 14px;
  font-weight: 500;
}

.lp-card-foot {
  margin-top: 8px;
  font-size: 12px;
  color: #888;
}
/* src/styles/LPCard.css 或者 ReserveDisplay.css */
.reserve-info {
  display: flex;
  align-items: center;
  gap: 4px;
  font-weight: 600;
}

.reserve-label {
  font-weight: 700;
  margin-right: 4px;
}
