/* src/styles/RemoveLiquidity.css */
.remove-liquidity {
  padding: 0 12px; /* 上下 0px，左右 12px */
  border-radius: 12px;
  background: #f4f4f9;
   font-size: 14px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}


.input-group {
  margin-bottom: 20px;
}

.input-item {
  margin-bottom: 12px;
}

.input-label {
  font-weight: 600;
  margin-bottom: 6px;
}

input {
  width: 100%;
  padding: 10px;
  border-radius: 6px;
  border: 1px solid #e5e7eb;
  font-size: 14px;
}

.action-btn {
  background-color: #007bff;
  color: white;
  border: none;
  padding: 12px;
  font-size: 12px;
  border-radius: 6px;
  cursor: pointer;
  width: 100%;
  font-weight: bold;
}

.action-btn:disabled {
  background-color: #b0c4de;
  cursor: not-allowed;
}

.hint {
  margin-top: 10px;
  color: #999;
  font-size: 12px;
}
