.audit-page {
  background: linear-gradient(180deg, #05090b, #0a1116);
  color: #e8f3ee;
  min-height: 100vh;
  text-align: center;
  padding: 50px 10px;
  font-family: 'Inter', 'PingFang SC', sans-serif;
  font-size: 13px;
  line-height: 1.8;
}

h1 {
  background: linear-gradient(90deg, #00ffa3, #00e5ff);
  background-clip: text;              /* ✅ 标准属性 */
  -webkit-background-clip: text;      /* ✅ Chrome / Safari 专用 */
  color: transparent;                 /* ✅ 标准透明文字 */
  -webkit-text-fill-color: transparent; /* ✅ WebKit 透明文字 */
}


.audit-page p {
  color: #b4c3c8;
}

.audit-desc {
  max-width: 700px;
  margin: 10px auto 20px;
}

.audit-placeholder {
  background: rgba(0,255,180,0.05);
  border: 1px solid rgba(0,255,180,0.15);
  border-radius: 10px;
  max-width: 480px;
  margin: 20px auto;
  padding: 20px;
  color: #00ffa3;
  box-shadow: 0 0 12px rgba(0,255,150,0.1);
}

.audit-note {
  color: #a8c2bc;
  font-size: 12.5px;
  margin-top: 20px;
}
