/* src/styles/ReserveDisplay.css */

/* 当前池储备容器 */
.reserve-info {
  background-color: #1C1C1C;
  padding: 12px;
  border-radius: 12px;
  margin-top: 10px;
  font-size: 12px;
  box-shadow: 0 0 10px rgba(0, 255, 255, 0.5);
  border: 1px solid #00FFEF;
  text-align: left;
}

.reserve-item {
  display: flex;
  justify-content: space-between;  /* 左右对齐 */
  align-items: center;
}

.reserve-label {
  font-size: 12px;
  color: #ffffff;
}

.reserve-value {
  font-weight: 600;
  font-size: 12px;
  color: #ffffff;
  /* text-shadow: 0 0 5px #00FFEF; */
  margin-left: auto;  /* 将数值部分推向右侧 */
}

.reserve-info:hover {
  background-color: #2D2D2D;
}
