.wp-wrapper {
  background: linear-gradient(180deg, #05080a 0%, #0a1116 100%);
  color: #e5f5f0;
  font-size: 13px;
  line-height: 1.8;
}

.wp-banner {
  background: radial-gradient(circle at top center, rgba(0,255,200,0.1), transparent 70%);
  padding: 60px 20px 40px;
  text-align: center;
  border-bottom: 1px solid rgba(0,255,180,0.1);
}

.wp-titlebox h1 {
  font-size: 2rem;
  background: linear-gradient(90deg, #00ffa3, #00e5ff);
  background-clip: text;              /* ✅ 标准属性 */
  -webkit-background-clip: text;      /* ✅ 兼容 Chrome / Safari */
  -webkit-text-fill-color: transparent;
  color: transparent;                 /* ✅ 标准写法，兼容 Firefox */
  margin-bottom: 8px;
}


.wp-subtitle {
  color: #b0c8c3;
  font-size: 0.9rem;
  letter-spacing: 0.8px;
}

.wp-content {
  max-width: 900px;
  margin: 0 auto;
  padding: 40px 20px 100px;
}

.wp-section {
  margin-bottom: 40px;
}

.wp-section h2 {
  border-left: 3px solid #00ffa3;
  padding-left: 12px;
  color: #00eaff;
  font-size: 1.1rem;
  margin-bottom: 14px;
}

.wp-section p {
  color: #d7efea;
  margin-bottom: 10px;
  text-align: justify;
}

.wp-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 16px;
}

.wp-card {
  background: rgba(255, 255, 255, 0.04);
  border: 1px solid rgba(0, 255, 200, 0.1);
  border-radius: 12px;
  padding: 16px 18px;
  transition: 0.25s;
}

.wp-card h3 {
  color: #00ffa3;
  margin-bottom: 6px;
  font-size: 0.95rem;
}

.wp-card p {
  font-size: 13px;
  color: #cfecec;
}

.wp-card:hover {
  border-color: rgba(0, 255, 200, 0.3);
  box-shadow: 0 0 16px rgba(0, 255, 180, 0.12);
  transform: translateY(-2px);
}

.wp-tokenomics {
  margin-top: 10px;
  border: 1px solid rgba(0, 255, 160, 0.15);
  border-radius: 10px;
  overflow: hidden;
}

.wp-tokenomics-item {
  display: flex;
  justify-content: space-between;
  padding: 8px 14px;
  border-bottom: 1px solid rgba(255,255,255,0.06);
}

.wp-tokenomics-item:last-child {
  border-bottom: none;
}

.wp-tokenomics-item span:first-child {
  color: #bdeee0;
}

.wp-tokenomics-item span:last-child {
  color: #00ffa3;
  font-weight: bold;
}

.wp-note {
  font-size: 13px;
  color: #a9c7bf;
  margin-top: 10px;
}

.wp-list li {
  margin-bottom: 6px;
  color: #cfecec;
}

.wp-section ul {
  margin: 0;
  padding-left: 20px;
}

.wp-section li {
  margin-bottom: 6px;
}

blockquote {
  background: rgba(0, 255, 180, 0.05);
  border-left: 3px solid #00ffa3;
  padding: 12px 18px;
  font-style: italic;
  color: #bdf7e3;
  border-radius: 6px;
}

.wp-section blockquote {
  background: rgba(255, 255, 255, 0.03);
  border-left: 3px solid #00ffa3;
  padding: 10px 16px;
  color: #c9f8ea;
  font-style: italic;
  margin: 14px 0;
}

.wp-contract {
  text-align: center;
  font-size: 13px;
  margin-top: 50px;
  color: #aaa;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 6px;
}

.wp-address {
  color: #fff;
  font-weight: 600;
}

.wp-copy {
  cursor: pointer;
  color: #00ffa3;
  transition: 0.2s;
}

.wp-copy:hover {
  color: #00eaff;
  transform: scale(1.1);
}

.copy-tip {
  position: fixed;
  bottom: 70px;
  left: 50%;
  transform: translateX(-50%);
  background: #00ffa3;
  color: #000;
  padding: 6px 14px;
  border-radius: 6px;
  font-size: 13px;
  opacity: 0.95;
  animation: fadeOut 1.3s ease forwards;
}

@keyframes fadeOut {
  0% { opacity: 1; }
  85% { opacity: 1; }
  100% { opacity: 0; transform: translate(-50%, 20px); }
}
