.contact-page {
  background: linear-gradient(180deg, #05090b, #0a1116);
  color: #e8f3ee;
  min-height: 100vh;
  font-family: 'Inter', 'PingFang SC', sans-serif;
  text-align: center;
  padding: 50px 20px;
  line-height: 1.8;
}

.contact-page h1 {
  background: linear-gradient(90deg, #00ffa3, #00e5ff);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  -webkit-text-fill-color: transparent;
  font-size: 1.6rem;
  margin-bottom: 10px;
}

.contact-subtitle {
  color: #a8c2bc;
  font-size: 13px;
  margin-bottom: 30px;
}

.contact-sections {
  max-width: 900px;
  margin: 0 auto;
  text-align: left;
}

.contact-sections section {
  background: rgba(0, 255, 180, 0.03);
  border: 1px solid rgba(0, 255, 180, 0.12);
  border-radius: 12px;
  padding: 20px;
  margin-bottom: 20px;
  box-shadow: 0 0 15px rgba(0, 255, 150, 0.05);
}

.contact-sections h2 {
  color: #00ffa3;
  font-size: 14px;
  margin-bottom: 8px;
}

.contact-sections p,
.contact-sections li {
  font-size: 13px;
  color: #d0e3de;
}

.contact-links {
  list-style: none;
  padding: 0;
}

.contact-links li {
  margin: 5px 0;
}

.contact-links a {
  color: #00eaff;
  text-decoration: none;
  font-size: 13px;
  transition: 0.2s;
}
.contact-links a:hover {
  color: #00ffa3;
  text-decoration: underline;
}

.contact-note {
  color: #97aaa7;
  font-size: 12px;
  margin-top: 6px;
}

.contact-footer {
  margin-top: 40px;
  color: #78938f;
  font-size: 12px;
  text-align: center;
}

/* 📱 移动端优化 */
@media (max-width: 768px) {
  .contact-sections section {
    padding: 16px;
  }
  .contact-sections h2 {
    font-size: 13.5px;
  }
  .contact-sections p,
  .contact-sections li {
    font-size: 12.5px;
  }
}
