/* ========== 页面基础 ========== */
.docs-page {
  background: linear-gradient(180deg, #05090b 0%, #0a1116 100%);
  color: #e8f3ee;
  font-family: 'Inter', 'PingFang SC', sans-serif;
  line-height: 1.7;
  font-size: 13px; /* ✅ 全局基础字体 */
  min-height: 100vh;
  position: relative;
  overflow-x: hidden;
}

/* ========== 顶部标题区 ========== */
.docs-header {
  text-align: center;
  padding: 50px 20px 30px;
  border-bottom: 1px solid rgba(0, 255, 180, 0.1);
}

.docs-header h1 {
  font-size: 1.6rem; /* ✅ 约 16px～17px */
  background: linear-gradient(90deg, #00ffa3, #00e5ff);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  -webkit-text-fill-color: transparent;
  margin-bottom: 4px;
}

.docs-header p {
  color: #b4c3c8;
  font-size: 13px; /* ✅ 次级文字 */
}

.wp-link {
  display: inline-block;
  margin-top: 12px;
  background: rgba(0, 255, 180, 0.08);
  border: 1px solid rgba(0, 255, 180, 0.25);
  border-radius: 6px;
  padding: 6px 12px;
  color: #00ffa3;
  text-decoration: none;
  font-size: 12.5px;
  transition: 0.25s;
}
.wp-link:hover {
  background: rgba(0, 255, 180, 0.2);
}

/* ========== 主内容区域 ========== */
.docs-content {
  max-width: 880px;
  margin: 0 auto;
  padding: 30px 18px 80px;
  font-size: 13px;
}

.docs-content h2 {
  color: #00eaff;
  border-left: 2px solid #00ffa3;
  padding-left: 8px;
  margin-top: 36px;
  margin-bottom: 8px;
  font-size: 15px; /* ✅ 小标题 */
}

.docs-content h3 {
  color: #00ffa3;
  margin-top: 10px;
  font-size: 13.5px;
}

.docs-content p,
.docs-content li {
  font-size: 13px;
  color: #dcefed;
}

table {
  width: 100%;
  border-collapse: collapse;
  font-size: 12.5px;
  margin-top: 8px;
}

th, td {
  border: 1px solid rgba(255,255,255,0.08);
  padding: 5px 8px;
  text-align: left;
}

th {
  background: rgba(0,255,180,0.05);
  color: #00ffa3;
  font-size: 12.5px;
}

/* ========== 桌面右侧目录 ========== */
.docs-toc {
  position: fixed;
  top: 90px;
  right: 35px;
  width: 180px;
  background: rgba(10, 18, 20, 0.85);
  border: 1px solid rgba(0, 255, 180, 0.15);
  border-radius: 10px;
  padding: 10px 12px;
  backdrop-filter: blur(6px);
  max-height: 72vh;
  overflow-y: auto;
  box-shadow: 0 0 10px rgba(0, 255, 180, 0.08);
  z-index: 100;
}

.toc-title {
  color: #00ffa3;
  font-weight: 600;
  text-align: center;
  font-size: 12.5px;
  margin-bottom: 8px;
}

.docs-toc ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.docs-toc li {
  padding: 5px 6px;
  border-radius: 5px;
  cursor: pointer;
  transition: 0.25s;
  color: #cfecec;
  font-size: 12.5px;
}
.docs-toc li:hover {
  background: rgba(0, 255, 180, 0.1);
  color: #00ffa3;
}
.docs-toc li.active {
  background: rgba(0, 255, 180, 0.18);
  color: #00ffa3;
  font-weight: 600;
}

/* 桌面端显示 */
.desktop-only { display: block; }
.mobile-only { display: none; }

/* ========== 手机端适配 ========== */
@media (max-width: 992px) {
  .desktop-only { display: none; }
  .mobile-only { display: flex; }

  .mobile-toc-btn {
    position: fixed;
    bottom: 20px;
    right: 20px;
    background: #00ffa3;
    color: #000;
    width: 45px;
    height: 45px;
    border-radius: 50%;
    align-items: center;
    justify-content: center;
    font-size: 12px; /* ✅ 按钮小一点 */
    box-shadow: 0 0 15px rgba(0,255,150,0.4);
    border: none;
    z-index: 9999;
    cursor: pointer;
  }

  .mobile-toc-overlay {
    position: fixed;
    inset: 0;
    background: rgba(0,0,0,0.6);
    z-index: 9998;
    display: flex;
    justify-content: flex-end;
  }

.mobile-toc {
  background: #0b1214;
  width: 40%;           /* ← 从 80% 改为 40%，缩小一半 */
  max-width: 220px;     /* 可以略调小些 */
  height: 100%;
  padding: 12px;
  border-left: 1px solid rgba(0,255,180,0.2);
  display: flex;
  flex-direction: column;
  animation: slideIn 0.3s ease;
}



  @keyframes slideIn {
    from { transform: translateX(100%); }
    to { transform: translateX(0); }
  }

  .mobile-toc-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #00ffa3;
    margin-bottom: 6px;
    font-size: 11.5px;
    font-weight: 600;
  }

  .mobile-toc-header button {
    background: none;
    border: none;
    color: #00ffa3;
    font-size: 18px;
    cursor: pointer;
  }

  .mobile-toc ul {
    list-style: none;
    padding: 0;
    margin: 0;
    overflow-y: auto;
  }

  .mobile-toc li {
    padding: 8px;
    margin-bottom: 5px;
    border-radius: 6px;
    cursor: pointer;
    transition: 0.2s;
    font-size: 13px;
  }

  .mobile-toc li:hover {
    background: rgba(0,255,180,0.1);
    color: #00ffa3;
  }

  .mobile-toc li.active {
    background: rgba(0,255,180,0.15);
    color: #00ffa3;
    font-weight: 600;
  }
}
