/* =========================================================
   Layout
   ========================================================= */
.liquidity-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 16px;
  margin-top: 8px;
}



/* =========================================================
   Action: 刷新按钮
   ========================================================= */
.refresh-text-btn {
  background: none;
  border: 1px solid #666;
  color: #fff;
  border-radius: 8px;
  padding: 6px 12px;
  font-size: 14px;
  cursor: pointer;
  transition: all 0.2s;
  -webkit-tap-highlight-color: transparent;
  user-select: none;
}
.refresh-text-btn:hover {
  background: rgba(255, 255, 255, 0.1);
}

/* =========================================================
   主题变量（可按需调整）
   ========================================================= */
:root {
  --claim-primary: #22c55e;         /* 主色（绿色） */
  --claim-primary-hover: #16a34a;   /* 悬停 */
  --claim-primary-active: #15803d;  /* 按下 */
  --claim-text: #ffffff;            /* 文字色 */
  --claim-shadow: 0 6px 16px rgba(34, 197, 94, 0.25);
  --claim-shadow-hover: 0 8px 18px rgba(34, 197, 94, 0.35);
  --claim-shadow-disabled: 0 0 0 rgba(0,0,0,0);
  --claim-border: rgba(22, 163, 74, 0.2);
}

/* =========================================================
   Action: 领取按钮
   ========================================================= */
.claim-btn {
  min-width: 160px;
  padding: 10px 18px;
  border-radius: 12px;
  border: 1px solid var(--claim-border);
  background: linear-gradient(180deg, var(--claim-primary), var(--claim-primary-active));
  color: var(--claim-text);
  font-weight: 800;
  letter-spacing: .2px;
  cursor: pointer;
  box-shadow: var(--claim-shadow);
  transition: transform .06s ease, box-shadow .15s ease, filter .15s ease, opacity .15s ease;
  user-select: none;
  -webkit-tap-highlight-color: transparent;
}
.claim-btn:hover {
  transform: translateY(-1px);
  box-shadow: var(--claim-shadow-hover);
  filter: brightness(1.02);
}
.claim-btn:active {
  transform: translateY(0);
  box-shadow: var(--claim-shadow);
  filter: brightness(.98);
}
.claim-btn.is-disabled,
.claim-btn:disabled {
  opacity: .6;
  cursor: not-allowed;
  transform: none;
  box-shadow: var(--claim-shadow-disabled);
  filter: none;
}
/* 小屏手势：禁用 hover 动画，避免抖动 */
@media (hover: none) {
  .claim-btn:hover {
    transform: none;
    box-shadow: var(--claim-shadow);
  }
}

/* =========================================================
   顶部奖励池（字号/权重）
   ========================================================= */
.reward-container .reward-text {
  font-size: 28px;
  font-weight: 800;
  line-height: 1.2;
}
.reward-container .per-second-text {
  font-size: 12px;
}
.reward-container .apr-text {
  font-size: 13px;
  font-weight: 700;
}