/* src/styles/BalanceDisplay.css */

/* 当前余额的容器 */
.deposit-info {
  background-color: #fff;
  padding: 12px;
  border-radius: 12px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  margin-top: 8px;
  text-align: left;
  font-size: 14px;
}

/* 每一项余额的行 */
.balance-item {
  display: flex;
  justify-content: flex-start;  /* Aligns the label and value at the start */
  margin-bottom: 6px;
  align-items: center;
}

/* 标签的样式（例如 LP:、USDT:、STR:） */
.balance-item span:first-child {
  font-size: 14px;
  padding-right: 15px;
  font-weight: bold;  /* Make the value bold */
}

/* 数值的样式，右对齐 */
.balance-value {
  margin-left: auto;  /* This will push the number to the right */
  text-align: right;  /* Ensure it's aligned to the right */
}
