.profile-container {
  padding: 12px;
  max-width: 420px;
  margin: 0 auto;
  color: #000000;
  background: rgb(255, 255, 255);

  box-shadow: 0 4px 20px rgba(0,0,0,0.3);
  font-family: "Segoe UI", sans-serif;
}

.profile-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 8px;
}

.profile-header h2 {
  font-size: 20px;
  font-weight: 600;
}

.connect-btn {
  padding: 6px 14px;
  border: none;
  border-radius: 12px;
  background: linear-gradient(90deg, #00c6ff, #0072ff);
  color: white;
  cursor: pointer;
  transition: 0.3s;
}

.connect-btn:hover {
  opacity: 0.85;
}

.wallet-info {
  background: rgba(255,255,255,0.05);
  padding: 6px 6px;
  border-radius: 12px;
  margin-bottom: 20px;
  font-size: 14px;
}

.token-list {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.token-item {
  display: flex;
  align-items: center;
  background: rgba(255,255,255,0.05);
  padding: 10px 14px;
  border-radius: 12px;
}

.token-icon {
  width: 28px;
  height: 28px;
  margin-right: 10px;
  border-radius: 50%;
  background: rgba(255,255,255,0.1);
}

.token-details {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.token-symbol {
  font-size: 15px;
  font-weight: 500;
}

.token-balance {
  font-size: 14px;
  font-weight: 400;
  color: #000000;
}
.profile-container {
  position: relative; /* 关键，loading 层以此为基准定位 */
  padding-bottom: 60px; /* 确保底部导航条不被覆盖，可根据实际底部高度调整 */
}

/* 局部 loading 层 */
.profile-loading-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(255, 255, 255, 0.6); /* 半透明遮罩，内容可见 */
  z-index: 10; /* 确保覆盖内容区域 */
  pointer-events: none; /* 保证点击事件透传，底部导航可点击 */
}
